.footer {
  margin: 5.7rem auto;
  padding-bottom: 5.7rem;
  display: flex;
  flex-direction: column;
  gap: 3.8rem;

  @include tablet {
    margin: 7.7rem auto;
    padding-bottom: 7.7rem;
    flex-direction: row;
    gap: 5.8rem;
  }
  @include desktop{
    gap: 11.7rem;
  }

  .social-media {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    @include tablet {
      flex-direction: row;
      flex-wrap: wrap;
    }

    .link-social {
      padding: 1.3rem 2.3rem 1.2rem 2.3rem;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 0.9rem;
      border: 1px solid $gray-line;
      border-radius: 0.4rem;
      width: 25rem;
      font-size: 1.6rem;
      font-style: normal;
      font-weight: 300;
      line-height: 24px;
      color: $text-color-card;
      svg path{
        fill: $text-color-card;
      }
      transition: .5s all ease;
      &:hover{
        background-color: $blue-tec;
        color: $white;
        svg path{
          fill: $white;
        }
      }
    }
  }
}
