html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

::-webkit-scrollbar {
  display: none;
}
body {
  animation: fade-in 1s ease-in-out;
  font-family: $font-principal;
  font-size: 1.6rem;
  line-height: 1.8;
  height: 100vh;

  background: $background-dark;

  transition: 0.3s all ease;
}

strong {
  // color: $blue-text;
  font-weight: 600;
}

.contenedor {
  width: 90%;
  @include desktop{
    width: 95%;
  }
  max-width: 1078px;
  margin: 0 auto;
}

p {
  color: $white;
  font-size: 1.6rem;
  margin: 0;
}

a {
  text-decoration: none;
  color: $white;
  font-size: 1.8rem;
}
span {
  color: $white;
}

svg {
  fill: $white;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: $font-principal;
  margin: 0;
  color: $white;
}

h1 {
  font-size: 4rem;
  @include tablet {
    font-size: 5.8rem;
  }
  @include desktop {
    font-size: 9.6rem;
  }
}

h2 {
  font-size: 1.8rem;
  @include tablet {
    font-size: 2.4rem;
  }
}
h3 {
  font-size: 2.4rem;
}
h4 {
  font-size: 2.6rem;
}

.line {
  height: 0.03rem;
  background: $text-color;
  max-width: 100%;
  width: auto;
  max-width: 100%;
  margin: 0;
  @include desktop {
    width: 95%;
    max-width: 1078px;
    margin: 0 auto;
  }
}
