.skills {
  margin: 6.3rem auto;
  display: flex;
  flex-direction: column;
  gap: 3.8rem;
  @include tablet {
    margin: 7.5rem auto;
    flex-direction: row;
    gap: 5.8rem;
  }
  @include tablet {
    margin: 10.5rem auto;
    gap: 9.9rem;
  }
  .contenedor-skills {
    // display: flex;
    // flex-direction: column;
    // gap: 2.3rem;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 2.3rem;
    @include tablet {
      flex: 1;
      gap: 3.9rem;
    }
    .skill {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      align-items: center;
      @include tablet {
        flex-direction: row;
        align-items: center;
        gap: 2.9rem;
      }
      .bar-por {
        height: 0.8rem;
        max-width: 360px;
        background: $gray;
        border-radius: 10rem;
        position: relative;
        @include tablet {
          flex: 1;
        }
        .por-line {
          border-radius: 10rem;
          position: absolute;
          height: 100%;
          width: 80%;
          background-color: $blue-bar;
        }
      }
      i{
        font-size: 4rem;
        color: $white;
        transition: .3s transform ease;
        &:hover{
          transform: scale(1.1);
        }
      }
      p {
        font-style: normal;
        font-weight: 300;
        line-height: 24px;
        text-transform: uppercase;
        @include desktop{
          font-size: 1.8rem;
          font-weight: 400;
        }
      }
    }
  }
}
