.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 6.8rem;
  @include tablet {
    height: 9.2rem;
  }
  @include desktop {
    height: 12.3rem;
  }

  .header-logo {
    span {
      font-size: 1.8rem;
      @include tablet {
        font-size: 2.4rem;
      }
    }
  }

  .header-nav {
    position: relative;
    .list-nav {
      z-index: 200;
      display: flex;
      flex-direction: column;
      gap: 2rem;
      position: absolute;
      top: 5rem;
      right: -50%;
      position: fixed;
      margin: 0;
      padding: 0;
      list-style: none;
      text-align: right;
      transition: 0.5s right ease;
      padding: 0.3rem 0.3rem 0.3rem 0.3rem;
      // border: 0.1rem solid $gray;
      border-radius: 0.8rem;

      @include tablet {
        position: relative;
        flex-direction: row;
        align-items: center;
        top: auto;
        right: auto;
        text-align: center;
        padding: 0;
        border: none;
        gap: 1rem;
      }

      .item {
        a {
          @include tablet {
            padding: 1rem 2.3rem;
            border-radius: 1rem;
            border: 0.1rem solid $background-dark;
            transition: 0.5s all ease;
            &:hover {
              border-color: $gray;
              color: $white;
              box-shadow: $shadow-dark;
            }
          }
        }
      }
      //   background: #1a1a1a38;
    }

    .mostrar {
      margin-right: 1.3rem;
      right: 0;
    }



    .container-option {
      display: flex;
      gap: 1.5rem;
      .btn-mode,
      .btn-bar {
        border: none;
        background: transparent;
        padding: 0;
        cursor: pointer;

        svg {
          width: 2.4rem;
          height: 2.4rem;
        }
      }
      .btn-mode {
        width: 2.4rem;
        height: 2.4rem;
        background-image: url(/build/img/luna.svg);
        background-position: center;
        background-repeat: no-repeat;
      }
      .bg-sol {
        background-image: url(/build/img/sol.svg);
      }
      .btn-bar {
        display: block;
        width: 2.4rem;
        height: 2.4rem;
        background-image: url(/build/img/icon-bar-dark.svg);
        background-position: center;
        background-repeat: no-repeat;
        @include tablet {
          display: none;
        }
      }
      .bg-x {
        background-image: url(/build/img/icon-x-dark.svg);
      }
    }
  }
}

