.projects {
  margin-top: 7.2rem;
  margin-bottom: 6.3rem;
  display: flex;
  flex-direction: column;
  gap: 3.8rem;

  @include tablet {
    margin-top: 9.6rem;
    margin-bottom: 7.5rem;
    gap: 5.1rem;
  }
  @include desktop {
    margin-top: 12rem;
    margin-bottom: 10.5rem;
  }

  .contenedor-project {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 3.6rem;
    @include tablet {
      gap: 4.5rem;
    }
    @include desktop {
      gap: 5.1rem;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 5.7rem;
      row-gap: 6rem;
    }
    .card-project {
      border-radius: 0.4rem;
      display: flex;
      flex-direction: column;
      gap: 3.1rem;
      transition: 0.5s box-shadow ease;
      &:hover {
        box-shadow: $shadow-dark;
      }
      .img-card {
        overflow: hidden;
        border-radius: 0.4rem 0.4rem 0 0;
        img {
          border-radius: 0.4rem 0.4rem 0 0;
          transition: transform 0.3s ease;
          &:hover {
            transform: scale(1.05);
          }
        }
      }
      .info-card {
        padding: 0 1rem 1rem 1rem;
        .title-info {
          margin-bottom: 1.2rem;
          h3 {
            font-style: normal;
            font-weight: 300;
            line-height: normal;
            font-size: 2rem;
            @include tablet {
              font-size: 2.4rem;
            }
          }
          display: flex;
          justify-content: space-between;
          align-items: center;

          .contenedor-link {
            display: flex;
            align-items: center;
            gap: 1.9rem;
            a {
              display: flex;
              align-items: center;
              transition: .3s transform ease;
              &:hover {
                transform: scale(1.1);
              }
            }
          }
        }
        .description-card {
          color: $text-color-card;
          font-style: normal;
          font-weight: 300;
          line-height: 24px;
          margin-bottom: 1.2rem;
          @include tablet {
            max-width: 453px;
          }
        }
        span {
          color: $blue-text;
          font-size: 14px;
          font-style: normal;
          font-weight: 300;
          line-height: 24px;
          text-transform: uppercase;
        }
      }
    }
  }
}

.title-section {
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  text-transform: uppercase;
}
